<template>
    <div :class="classFactory" @click="onClick">
        <div class="text">
            {{ text }}
        </div>
    </div>
</template>

<script>
  export default {
    name: 'QinButton',
    props: {
      text: { type: String, default: 'Button' },
      active: { type: Boolean, default: false },
      offset: { type: Object, default: () => ({ x: 0, y: 0 }) },
      appearance: { type: String, default: 'solid' },
      size: { type: String, default: 'normal' }
    },
    methods: {
      onClick (e) {
        this.$emit('click', e)
      }
    },
    computed: {
      classFactory () {
        let c = ['qin-button']
        c.push(this.appearance)
        c.push(this.size)
        if (this.active) c.push('button-active')
        return c
      }
    }
  }
</script>

