<template>
    <div class="full-header-layout">
        <transition name="fade">
            <div class="black-mask"
                 v-if="showGeneralHeader && showMask"
                 @click="onMaskClick"></div>
        </transition>

        <ExtraHeader ref="extra-header"/>
        <transition name="fade">
            <GeneralHeader ref="general-header"
                           v-if="showGeneralHeader"
                           v-on:change="onNavBarChange"/>
        </transition>
        <RouterView class="view-content"/>
    </div>
</template>

<script>
  import ExtraHeader from '../components/header/ExtraHeader'
  import GeneralHeader from '../components/header/GeneralHeader'

  const WIDTH_SPLIT = 750

  export default {
    name: 'FullHeaderLayout',
    components: { ExtraHeader, GeneralHeader },
    data () {
      return {
        showGeneralHeader: false,
        showMask: false,
        bias: 60,
        statusPadding: Number(!!window.navigator.standalone) * 20
      }
    },
    methods: {
      onScroll () {
        let h = this.$refs['extra-header'].$el.clientHeight - this.bias
        this.showGeneralHeader = (window.scrollY - h > 0)
      },
      onNavBarChange (state) {
        this.showMask = state
      },
      onWindowResize () {
        this.bias = (window.innerWidth > WIDTH_SPLIT ? 120 : 60) + this.statusPadding
      },
      onMaskClick () {
        this.$refs['general-header'].onMoreButtonClick()
      }
    },
    created () {
      window.addEventListener('scroll', this.onScroll)
    },
    mounted () {
      this.bias = (window.innerWidth > WIDTH_SPLIT ? 120 : 60) + this.statusPadding
      window.addEventListener('resize', this.onWindowResize)
    },
    destroyed () {
      window.removeEventListener('scroll', this.onScroll)
      window.removeEventListener('resize', this.onWindowResize)
    }
  }
</script>

<style scoped lang="less">
    @media (min-width: 0) {
        .black-mask {
            width: 100%;
            height: 100vh;

            position: fixed;
            left: 0;
            top: 0;

            background-color: rgba(0, 0, 0, 0.4);
        }

        .view-content {
            margin: 0 auto;
        }
    }
</style>