<template>
    <div class="tiny-header-layout" :class="{'status-inner-padding': statusPadding}">
        <transition name="fade">
            <div class="black-mask"
                 v-if="showMask"
                 @click="onMaskClick"></div>
        </transition>
        <GeneralHeader ref="general-header"
                       v-on:change="onNavBarChange"/>
        <RouterView/>
    </div>
</template>

<script>
  import GeneralHeader from '../components/header/GeneralHeader'

  export default {
    name: 'TinyHeaderLayout',
    components: { GeneralHeader },
    data () {
      return {
        showMask: false,
        statusPadding: window.navigator.standalone
      }
    },
    methods: {
      onNavBarChange (state) {
        this.showMask = state
      },
      onMaskClick() {
        this.$refs['general-header'].onMoreButtonClick()
      }
    }
  }
</script>

