<template>
    <div class="general-header" :class="{'status-padding': statusPadding}">
        <div class="pc-container">
            <div class="brand">
                <img class="qin" src="../../assets/images/qin.svg" alt="">
                <img class="yiqinzhao" src="../../assets/images/yiqinzhao.svg" alt="">
            </div>
            <div class="actions">
                <HeaderMenu/>
            </div>
        </div>
        <div class="title">
            <div class="text">{{ title.en }}</div>
            <img class="more-button"
                 src="../../assets/images/more-button.svg" alt=""
                 @click="onMoreButtonClick">
        </div>
        <transition name="fade">
            <HeaderMenu v-if="showMenu" v-on:click="onMenuClick"/>
        </transition>
    </div>
</template>

<script>
  import HeaderMenu from './HeaderMenu'

  const map = {
    '/': { zh: '勤', en: 'Yiqin Zhao' },
    '/news': { zh: '闻', en: 'NEWS' },
    '/research': { zh: '研', en: 'RESEARCH' },
    '/project': { zh: '项', en: 'PROJECT' },
    '/blog': { zh: '文', en: 'BLOG' }
  }

  export default {
    name: 'GeneralHeader',
    components: { HeaderMenu },
    data () {
      return {
        showMenu: false,
        title: { zh: '勤', en: 'Yiqin Zhao' },
        statusPadding: window.navigator.standalone
      }
    },
    watch: {
      $route (to) {
        let r = to.path.split('/').slice(0, 2).join('/')
        this.title = map[r]
      }
    },
    methods: {
      onMoreButtonClick () {
        this.showMenu = !this.showMenu
        this.$emit('change', this.showMenu)
      },
      onMenuClick () {
        this.showMenu = !this.showMenu
        this.$emit('change', this.showMenu)
      }
    },
    mounted () {
      let r = this.$router.history.current.path.split('/').slice(0, 2).join('/')
      this.title = map[r]
    }
  }
</script>

