import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

Vue.config.productionTip = false
Vue.use(VueRouter)

import FullHeaderLayout from './layout/FullHeaderLayout'
import TinyHeaderLayout from './layout/TinyHeaderLayout'

import SimpleMarkdownView from './view/SimpleMarkdownView'
import ArticlesView from './view/BlogView'
import ArticlesReadingView from './view/BlogReadingView'

const router = new VueRouter({
  mode: 'hash',
  routes: [
    { path: '*', redirect: '/' },
    {
      path: '/',
      component: FullHeaderLayout,
      children: [{ path: '', component: SimpleMarkdownView }]
    },
    {
      path: '/news',
      component: TinyHeaderLayout,
      children: [{ path: '', component: SimpleMarkdownView }]
    },
    {
      path: '/research',
      component: TinyHeaderLayout,
      children: [{ path: '', component: SimpleMarkdownView }]
    },
    {
      path: '/project',
      component: TinyHeaderLayout,
      children: [{ path: '', component: SimpleMarkdownView }]
    },
    {
      path: '/blog',
      component: TinyHeaderLayout,
      children: [
        { path: '', component: ArticlesView },
        { path: ':name', component: ArticlesReadingView },
      ]
    },
    {
      path: '/cv',
      beforeEnter() {
        const a = document.createElement('a')
        a.setAttribute('href', 'https://dl.coden.hk/yiqin_zhao_cv.pdf')
        a.setAttribute('target', '_blank')
        a.click()
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  // const pathRoot = v => v.path.split('/').slice(0, 2).join('/')
  // if (pathRoot(to) !== pathRoot(from))
  window.scrollTo(0, 0)
  next()
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
