<template>
    <div class="simple-markdown-view">
        <transition-group name="fade">
            <div class="main-container markdown"
                 v-if="!loading"
                 v-html="content"
                 key="markdown">
            </div>
            <div class="boards-container"
                 key="boards"
                 v-if="!loading && this.boards">
                <div class="sticky-wrapper">
                    <BoardWidget :class="'boards ' + key"
                                 :title="key"
                                 :key="key"
                                 :content="value"
                                 v-for="(value, key) in boards"/>
                </div>
            </div>
        </transition-group>
    </div>
</template>

<script>
  import BoardWidget from '../components/BoardWidget'
  import * as dayjs from 'dayjs'

  export default {
    name: 'SimpleMarkdownView',
    components: { BoardWidget },
    data () {
      return {
        content: '',
        boards: [],
        loading: true
      }
    },
    methods: {
      dateFormatter: date => dayjs.unix(date).format('MMM D, YYYY'),
      async fetchData (url) {
        this.loading = true
        let res = await (await fetch(url)).json()
        this.content = res.content
        this.boards = res.boards
        this.loading = false
      }
    },
    beforeRouteEnter (to, from, next) {
      const dataMap = {
        '/': 'home',
        '/news': 'news',
        '/research': 'research',
        '/project': 'project',
      }
      next(vm => vm.fetchData(`/pages/${dataMap[to.path]}.json`))
    }
  }
</script>

