<template>
    <div class="articles-reading-view">
        <transition-group name="fade">
            <div class="header" v-if="!loading" key="title">
                <div class="title">{{ mdMeta.title }}</div>
                <div class="meta">
                    <i class="category">{{ mdMeta.category }}</i>
                    <i class="date">{{ dateFormatter(mdMeta.date) }}</i>
                </div>
            </div>
            <div class="content markdown"
                 v-if="!loading"
                 v-html="content"
                 key="markdown"></div>
        </transition-group>
    </div>
</template>

<script>
  import * as dayjs from 'dayjs'

  export default {
    name: 'BlogReadingView',
    data () {
      return {
        mdMeta: {},
        content: '',
        loading: true
      }
    },
    methods: {
      dateFormatter: date => dayjs.unix(date).format('MMM D, YYYY'),
      async fetchData (url) {
        let res = await (await fetch(url)).json()
        this.mdMeta = res.mdMeta
        this.content = res.content

        this.loading = false
      }
    },
    beforeRouteEnter (to, from, next) {
      next(vm => vm.fetchData(`/blog/${to.params.name}.json`))
    }
  }
</script>

