<template>
    <div class="menu">
        <div class="row">
            <RouterLink class="menu-router" to="/" tag="div"
                        v-on:click.native="onItemClick('home')">
                <QinButton class="menu-item"
                           text="HOME"
                           :active="activeButton === '/'"/>
            </RouterLink>
            <RouterLink class="menu-router" to="/news" tag="div"
                        v-on:click.native="onItemClick('news')">
                <QinButton class="menu-item"
                           text="NEWS"
                           :active="activeButton === '/news'"/>
            </RouterLink>

        </div>
        <div class="row">
            <RouterLink class="menu-router" to="/research" tag="div"
                        v-on:click.native="onItemClick('research')">
                <QinButton class="menu-item"
                           text="RESEARCH"
                           :active="activeButton === '/research'"/>
            </RouterLink>
            <RouterLink class="menu-router" to="/project" tag="div"
                        v-on:click.native="onItemClick('project')">
                <QinButton class="menu-item"
                           text="PROJECT"
                           :active="activeButton === '/project'"/>
            </RouterLink>
        </div>
        <div class="row">
            <RouterLink class="menu-router" to="/blog" tag="div"
                        v-on:click.native="onItemClick('blog')">
                <QinButton class="menu-item"
                           text="BLOG"
                           :active="activeButton === '/blog'"/>
            </RouterLink>
            <RouterLink class="menu-router" to="/cv" tag="div"
                        v-on:click.native="onItemClick('cv')">
                <QinButton class="menu-item"
                           text="CV"
                           :active="activeButton === '/cv'"/>
            </RouterLink>
        </div>
    </div>
</template>

<script>
  import QinButton from '../universal/QinButton'

  export default {
    name: 'HeaderMenu',
    components: { QinButton },
    data () {
      return {
        activeButton: ''
      }
    },
    watch: {
      $route (to) {
        this.activeButton = to.path
          .split('/')
          .slice(0, 2)
          .join('/')
      }
    },
    methods: {
      onItemClick (to) {
        this.$emit('click', to)
      }
    },
    mounted () {
      this.activeButton = this.$router.history.current.path
        .split('/')
        .slice(0, 2)
        .join('/')
    }
  }
</script>

