<template>
    <transition name="fade">
        <div class="blog-view">
            <div class="categories">
                <div class="title">categories</div>
                <div class="container">
                    <QinButton appearance="cavity"
                               size="nano"
                               class="category-button"
                               :active="categoryFilters.includes(item)"
                               v-on:click="onCategoryButtonClick(item)"
                               :text="item"
                               :key="index"
                               v-for="(item, index) in categoryFactory"/>
                </div>
            </div>

            <div class="articles">
                <AbstractItem :meta="item.mdMeta"
                              :abstract="item.abstract"
                              :key="index"
                              v-for="(item, index) in articleFactory"/>
            </div>
        </div>
    </transition>
</template>

<script>
  import AbstractItem from '../components/AbstractItem'
  import QinButton from '../components/universal/QinButton'

  export default {
    name: 'BlogView',
    components: { QinButton, AbstractItem, },
    data () {
      return {
        rawData: undefined,
        articles: [],
        categoryFilters: ['All']
      }
    },
    computed: {
      categoryFactory () {
        return ['All']
          .concat(this.articles
            .map(v => v.mdMeta.category)
            .filter((v, i, arr) => arr.indexOf(v) === i))
      },
      articleFactory () {
        if (this.categoryFilters.includes('All')) return this.articles
        else {
          return this.articles
            .filter(v => this.categoryFilters.includes(v.mdMeta.category))
        }
      }
    },
    methods: {
      onCategoryButtonClick (category) {
        this.categoryFilters = [category]
      }
    },
    async mounted () {
      let v = await (await fetch('/pages/blog-index.json')).json()
      this.rawData = JSON.stringify(v)
      this.articles = v

    }
  }
</script>

